#weather h2 {
    color: #FFF;
    font-size: 30px;
    font-weight: 300;
    margin: 0 0 8px;
    text-align: center;
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.15);
}

#weather ul {
    margin: 0;
    padding: 0;
}

#weather li {
    background: #FFF;
    background: rgba(255,255,255,0.90);
    border-radius: 5px;
    display: inline-block;
    font-size: 16px;
    padding: 5px;
}

#weather .currently {
    margin: 0 20px;
}
